.App {
    text-align: center;
    background-color: #282c34;
    background-image: url("assets/textures/checkeredGrunge.png");
    background-position: center;
    background-repeat: repeat;
    background-size: 218px;
}

.App-body {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    background: radial-gradient(transparent 40%, #000);
}

.gameStats {
    text-align: left;
    color: #282c34;
    list-style: none;
    padding: 10px;
    margin: 0 0 10px;
    width: 350px;
    text-shadow: hsla(0,0%,40%,.5) 0 -1px 0, hsla(0,0%,100%,.6) 0 2px 1px;
    background: #aaa;
    box-shadow: inset hsla(0,0%,15%, 1) 0 0px 0px 4px,
        /* border */ inset hsla(0,0%,15%, .8) 0 -1px 5px 4px,
        /* soft SD */ inset hsla(0,0%,0%, .25) 0 -1px 0px 7px,
        /* bottom SD */ inset hsla(0,0%,100%,.7) 0 2px 1px 7px,
        /* top HL */ hsla(0,0%, 0%,.15) 0 -5px 6px 4px;
}

.frame {
    position: relative;
    width: 445px;
    height: 666px;
    background: url("assets/textures/gameFrame.png") top right no-repeat;
}

.viewPort {
    height: 560px;
    width: 373px;
    margin: 36px 8px 10px 8px;
    position: relative;
    overflow: hidden;
}

.viewPort:after {
    content: '';
    position: absolute;
    width: 320px;
    top: 0;
    right: -1px;
    height: 20px;
    box-shadow: inset 0 6px 6px 0 black
}

.box {
    position: absolute;
    margin-bottom: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column-reverse;
}

.movementModal {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: black;
    opacity: 0.25;
    z-index: 100;
}

.row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.stage_base  {
    flex-grow: 0;
    flex-shrink: 0;
    width: 48px;
    height: 32px;
    line-height: 32px;
    margin: 0 8px 0 0;
    font-size: 12px;
    color: white;
    text-shadow:
        -1px -1px 0 #000,
        1px -1px 0 #000,
        -1px 1px 0 #000,
        1px 1px 0 #000;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.75);
}
.stage_0  { background-color: #50F246 }
.stage_1  { background-color: #49FC6F }
.stage_2  { background-color: #4EE695 }
.stage_3  { background-color: #49FCCB }
.stage_4  { background-color: #36F5F1 }
.stage_5  { background-color: #4BE4F2 }
.stage_6  { background-color: #4EC3FC }
.stage_7  { background-color: #5292E6 }
.stage_8  { background-color: #4E70FC }
.stage_9  { background-color: #433BF5 }
.stage_10 { background-color: #6733F2 }
.stage_11 { background-color: #9C35FC }
.stage_12 { background-color: #BF3BE6 }
.stage_13 { background-color: #FC35F7 }
.stage_14 { background-color: #F5228F }
.stage_15 { background-color: #F53702 }

.cell {
    flex-grow: 0;
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    line-height: 30px;
    text-align: center;
    box-sizing: border-box;
    background: darkgray;
    border-right-color: grey;
    border-bottom-color: grey;
    color: #282c34;
    box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.25);
}

.cell_basic {
    background-color: darkolivegreen;
    background-image: url("assets/textures/TX Tileset grass.png");
    background-position: -32px -32px;
    border-color: transparent;
    color: white;
    text-shadow:
        -1px -1px 0 #000,
        1px -1px 0 #000,
        -1px 1px 0 #000,
        1px 1px 0 #000;
}
.cell_swamp     { background-image: url("assets/textures/TX Tileset swamp.png"); }
.cell_woods     { background-image: url("assets/textures/TX Tileset woods.png"); }
.cell_mountains { background-image: url("assets/textures/TX Tileset mountains.png"); }
.cell_ruins     { background-image: url("assets/textures/TX Tileset ruins.png"); }
.cell_portal    { background-image: url("assets/textures/TX Tileset portal.png"); }
.cell_hell      { background-image: url("assets/textures/TX Tileset hell.png"); }

